'use strict';

/**
* Round a single-precision floating-point number to the nearest integer.
*
* @module @stdlib/math-base-special-roundf
*
* @example
* var roundf = require( '@stdlib/math-base-special-roundf' );
*
* var v = roundf( -4.2 );
* // returns -4.0
*
* v = roundf( -4.5 );
* // returns -4.0
*
* v = roundf( -4.6 );
* // returns -5.0
*
* v = roundf( 9.99999 );
* // returns 10.0
*
* v = roundf( 9.5 );
* // returns 10.0
*
* v = roundf( 9.2 );
* // returns 9.0
*
* v = roundf( 0.0 );
* // returns 0.0
*
* v = roundf( -0.0 );
* // returns -0.0
*
* v = roundf( Infinity );
* // returns Infinity
*
* v = roundf( -Infinity );
* // returns -Infinity
*
* v = roundf( NaN );
* // returns NaN
*/

// MODULES //

var main = require( './main.js' );


// EXPORTS //

module.exports = main;
